import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  Box,
  Button,
  IconButton,
  makeStyles,
  Typography,
} from "@material-ui/core";
import { useSnackbar } from "notistack";

import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import LinearProgress from "@material-ui/core/LinearProgress";
import Tooltip from "@material-ui/core/Tooltip";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import { ShoppingBag as ShoppingBagIcon } from "react-feather";

import ProductDialog, { ProductDialogMode } from "./ProductDialog";
import IngredientDialog, { IngredientDialogMode } from "./IngredientDialog";
import UploadSpecSheetBtn from "./UploadSpecSheetBtn";

import {
  Product,
  Ingredient,
  ReviewRequest,
} from "../../../reviewer/common/types";
import AddProductDialog from "./AddProductDialog";
import IngredientsView from "./IngredientsView";

interface FinishedProductsStepProps {
  reviewRequest: ReviewRequest;
}

// @TODO handle avatar
const FinishedProductsStep = ({ reviewRequest }: FinishedProductsStepProps) => {
  const classes = useStyles();
  const [loading, setLoading] = useState<boolean>(false);
  const { enqueueSnackbar } = useSnackbar();
  const [products, setProducts] = useState<Product[]>([]);

  useEffect(() => {
    getProducts();
  }, []);

  const getProducts = () => {
    setLoading(true);
    axios
      .post(`/api/client/review-request/${reviewRequest.id}/products`)
      .then(async (response) => {
        setLoading(false);
        if (response.status == 200 || response.status == 201) {
          console.log(response.data);
          setProducts(response.data);
        } else {
          console.log(response);
          enqueueSnackbar("Failed to retrieve products.", {
            variant: "error",
          });
        }
      })
      .catch((e) => {
        console.error(e);
        setLoading(false);
        enqueueSnackbar("Failed to retrieve products.", {
          variant: "error",
        });
      });
  };

  const handleProductAdd = (product: Product) => {
    setProducts([product, ...products]);
  };

  return (
    <Box className={classes.stepBox}>
      {loading && <LinearProgress />}
      <Typography
        variant="h3"
        style={{ marginBottom: 30, textAlign: "center" }}
      >
        Finished Products
      </Typography>
      <Box
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          marginBottom: 30,
        }}
      >
        <Typography>
          Click on the button at the right to add your products and ingredients
          to this request:
        </Typography>
        <AddProductDialog
          onProductAdd={handleProductAdd}
          reviewRequestId={reviewRequest.id as number}
          facilityId={reviewRequest.facility_id as number}
        />
      </Box>
      <List
        component="nav"
        aria-labelledby="nested-list-subheader"
        // subheader={
        //   <ListSubheader component="div" id="nested-list-subheader">
        //     Products and Ingredients
        //   </ListSubheader>
        // }
        className={classes.root}
      >
        {products.map((product: Product) => (
          <ProductItem product={product} />
        ))}
      </List>
    </Box>
  );
};

export default FinishedProductsStep;

function getSome<T>(array: Array<T>, some: number): Array<T> {
  return array.sort(() => Math.random() - Math.random()).slice(0, some);
}

export interface ProductItemProps {
  product: Product;
}

export function ProductItem({ product }: ProductItemProps) {
  const [open, setOpen] = React.useState<boolean>(false);
  const [specSheet, setSpecSheet] = React.useState<boolean>(false);
  const { enqueueSnackbar } = useSnackbar();

  const handleClick = () => {
    setOpen(!open);
  };

  const handleIngredientAdd = (ingredient: Ingredient) => {
    // cause refresh
    setOpen(!open);
    // setOpen(true);
  };

  // @TODO make real
  const uploadSpecSheet = () => {
    sleep(2000).then(() => {
      setSpecSheet(!specSheet);
      if (!specSheet)
        enqueueSnackbar("Product Spec Sheet uploaded successfully", {
          variant: "success",
        });
    });
  };

  return (
    <>
      <ListItem>
        <ListItemIcon>
          <ShoppingBagIcon />
        </ListItemIcon>
        <ListItemText primary={product.name} />
        {/* <Button
          variant={(specSheet && "contained") || "text"}
          color={(specSheet && "primary") || "default"}
          startIcon={<CloudUploadIcon />}
          style={{ marginRight: 15 }}
          onClick={uploadSpecSheet}
        >
          Product Spec Sheet
        </Button> */}
        <Tooltip title="A product specification sheet is a technical document that outlines the features of a product in detail. It explains and describes the product, what it is, what it does, what its components are and any special features it has. It also provides any other important additional information such as hazard and allergy warnings.">
          <UploadSpecSheetBtn productId={product.id as number} />
        </Tooltip>
        <span style={{ width: 15 }} />
        <IngredientDialog
          reviewRequestId={product.review_request_id as number}
          productId={product.id as number}
          onIngredientUpdate={handleIngredientAdd}
          mode={IngredientDialogMode.ADD}
        />
        <IconButton onClick={handleClick}>
          {open ? <ExpandLess /> : <ExpandMore />}
        </IconButton>
      </ListItem>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <IngredientsView productId={product.id as number} />
          {/* {ingredients.map((ingredient: Ingredient) => (
            <ListItem button className={classes.nested}>
              <ListItemIcon>
                <SubdirectoryArrowRightIcon />
              </ListItemIcon>
              <ListItemText
                primary={`${ingredient.name} (${"Demo Manufacturer"})`}
                // secondary="Demo Manufacturer"
              />
              <IconButton size="small">
                <DeleteIcon />
              </IconButton>
            </ListItem>
          ))} */}
        </List>
      </Collapse>
      {/* <ProductDialog
        mode={ProductDialogMode.EDIT}
        open={open}
        edit={product}
        onClose={handleProductDialogClose}
        onProductUpdate={_onProductUpdate}
      /> */}
    </>
  );
}

const useStyles = makeStyles((theme) => ({
  stepBox: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    // alignItems: "center",
  },
  stepBtn: {
    width: 500,
  },

  root: {},
  demo: {
    backgroundColor: theme.palette.background.paper,
  },
  title: {
    margin: theme.spacing(4, 0, 2),
  },

  nested: {
    paddingLeft: theme.spacing(4),
  },
}));

function sleep(time: number) {
  return new Promise((resolve) => setTimeout(resolve, time));
}
